.cloud1 {
  position: absolute;
  margin-left: 50%;
  margin-top: -200px;
}

.cloud2 {
  position: absolute;
  margin-top: -150px;
  margin-left: 80%;
}

.sky {
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 100%;
}

.sky .cloud {
  background: #ebf5fc;
  border-radius: 200px;
  -moz-border-radius: 200px;
  -webkit-border-radius: 200px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  height: 39px;
  margin-right: -102px;
  position: absolute;
  right: 0;
  width: 102px;
}

.sky .cloud.variant-1 {
  top: 19%;
  -webkit-transform: scale(0.2);
  -o-transform: scale(0.2);
  -moz-transform: scale(0.2);
  transform: scale(0.2);
  -webkit-animation: clouds-in-out 16s 0s infinite linear;
  animation: clouds-in-out 16s 0s infinite linear;
  -moz-animation: clouds-in-out 16s 0s infinite linear;
  -o-animation: clouds-in-out 16s 0s infinite linear;
}

.sky .cloud.variant-2 {
  top: 25%;
  -webkit-transform: scale(0.4);
  -o-transform: scale(0.4);
  -moz-transform: scale(0.4);
  transform: scale(0.4);
  -webkit-animation: clouds-in-out 17s 2s infinite linear;
  animation: clouds-in-out 17s 2s infinite linear;
  -moz-animation: clouds-in-out 17s 2s infinite linear;
  -o-animation: clouds-in-out 17s 2s infinite linear;
}

.sky .cloud.variant-3 {
  top: 35%;
  transform: scale(0.6);
  -webkit-transform: scale(0.6);
  -o-transform: scale(0.6);
  -moz-transform: scale(0.6);
  -webkit-animation: clouds-in-out 18s 4s infinite linear;
  animation: clouds-in-out 18s 4s infinite linear;
  -moz-animation: clouds-in-out 18s 4s infinite linear;
  -o-animation: clouds-in-out 18s 4s infinite linear;
}

.sky .cloud.variant-4 {
  top: 76%;
  transform: scale(0.8);
  -webkit-transform: scale(0.8);
  -moz-transform: scale(0.8);
  -o-transform: scale(0.8);
  -webkit-animation: clouds-in-out 19s 6s infinite linear;
  animation: clouds-in-out 19s 6s infinite linear;
  -moz-animation: clouds-in-out 19s 6s infinite linear;
  -o-animation: clouds-in-out 19s 6s infinite linear;
}

.sky .cloud.variant-5 {
  top: 55%;
  transform: scale(1);
  -webkit-transform: scale(1);
  -o-transform: scale(1);
  -moz-transform: scale(1);
  -webkit-animation: clouds-in-out 20s 11s infinite linear;
  animation: clouds-in-out 20s 11s infinite linear;
  -moz-animation: clouds-in-out 20s 11s infinite linear;
  -o-animation: clouds-in-out 20s 11s infinite linear;
}

.sky .cloud.variant-6 {
  top: 75%;
  transform: scale(0.7);
  -webkit-transform: scale(0.7);
  -moz-transform: scale(0.7);
  -o-transform: scale(0.7);
  -webkit-animation: clouds-in-out 22s 8s infinite linear;
  animation: clouds-in-out 22s 8s infinite linear;
  -moz-animation: clouds-in-out 22s 8s infinite linear;
  -o-animation: clouds-in-out 22s 8s infinite linear;
}

.sky .cloud.variant-7 {
  top: 100%;
  transform: scale(0.5);
  -webkit-transform: scale(0.5);
  -moz-transform: scale(0.5);
  -o-transform: scale(0.5);
  -webkit-animation: clouds-in-out 26s 7s infinite linear;
  animation: clouds-in-out 26s 7s infinite linear;
  -moz-animation: clouds-in-out 26s 7s infinite linear;
  -o-animation: clouds-in-out 26s 7s infinite linear;
}

.sky .cloud.variant-8 {
  top: 82%;
  transform: scale(0.6);
  -webkit-transform: scale(0.6);
  -moz-transform: scale(0.6);
  -o-transform: scale(0.6);
  -webkit-animation: clouds-in-out 24s 8s infinite linear;
  animation: clouds-in-out 24s 8s infinite linear;
  -moz-animation: clouds-in-out 24s 8s infinite linear;
  -o-animation: clouds-in-out 24s 8s infinite linear;
}

.sky .cloud.variant-9 {
  top: 120%;
  transform: scale(0.3);
  -webkit-transform: scale(0.3);
  -moz-transform: scale(0.3);
  -o-transform: scale(0.3);
  -webkit-animation: clouds-in-out 18s 8s infinite linear;
  animation: clouds-in-out 18s 8s infinite linear;
  -moz-animation: clouds-in-out 18s 8s infinite linear;
  -o-animation: clouds-in-out 18s 8s infinite linear;
}

.sky .cloud.variant-10 {
  top: 160%;
  transform: scale(0.3);
  -webkit-transform: scale(0.3);
  -moz-transform: scale(0.3);
  -o-transform: scale(0.3);
  -webkit-animation: clouds-in-out 18s 8s infinite linear;
  animation: clouds-in-out 18s 8s infinite linear;
  -moz-animation: clouds-in-out 18s 8s infinite linear;
  -o-animation: clouds-in-out 18s 8s infinite linear;
}

.sky .cloud.variant-11 {
  top: 225%;
  transform: scale(0.7);
  -webkit-transform: scale(0.7);
  -moz-transform: scale(0.7);
  -o-transform: scale(0.6);
  -webkit-animation: clouds-in-out 14s 8s infinite linear;
  animation: clouds-in-out 14s 8s infinite linear;
  -moz-animation: clouds-in-out 14s 8s infinite linear;
  -o-animation: clouds-in-out 14s 8s infinite linear;
}

.sky .cloud:before, .sky .cloud:after {
  background: #ebf5fc;
  border-radius: 100px;
  content: '';
  height: 45px;
  left: 10px;
  position: absolute;
  top: -10px;
  width: 60px;
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
  -moz-transform: rotate(30deg);
  -o-transform: rotate(30deg);
}

.sky .cloud:after {
  height: 60px;
  left: auto;
  right: 12px;
  top: -24px;
  width: 60px;
}

@keyframes clouds-in-out {
  0% {
    right: -5%;
  }
  100% {
    right: 105%;
  }
}

@-webkit-keyframes clouds-in-out {
