.login-container ::-webkit-input-placeholder {
  /* WebKit browsers */
  color: white;
  opacity: 1;
  font-weight: 300;
}

.login-container :-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: white;
  opacity: 1;
  font-weight: 300;
}

.login-container :-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: white;
  font-weight: 300;
}

.login-container form {
  padding: 20px 0;
  position: relative;
  z-index: 2;
}

.login-container form input:not([type="checkbox"]) {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  outline: 0;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background-color: rgba(255, 255, 255, 0.2);
  width: 250px;
  border-radius: 3px;
  padding: 10px 15px;
  margin: 0 auto 10px auto;
  display: block;
  text-align: center;
  font-size: 18px;
  color: white;
  -webkit-transition-duration: 0.25s;
  -o-transition-duration: 0.25s;
  -moz-transition-duration: 0.25s;
  transition-duration: 0.25s;
  font-weight: 300;
}

.login-container form input:not([type="checkbox"]):hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.login-container form input:not([type="checkbox"]):focus {
  background-color: white;
  width: 300px;
  color: #000000;
}

.login-container form button {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  outline: 0;
  background-color: white;
  border: 0;
  padding: 10px 15px;
  color: #000;
  border-radius: 3px;
  width: 250px;
  cursor: pointer;
  font-size: 18px;
  -webkit-transition-duration: 0.25s;
  -o-transition-duration: 0.25s;
  -moz-transition-duration: 0.25s;
  transition-duration: 0.25s;
}

.login-container form button[disabled] {
  opacity: 0.5;
  cursor: auto;
}

.login-container form button[disabled]:hover {
  opacity: 0.5;
  background-color: white;
  cursor: auto;
}

.login-container form button:hover {
  background-color: $primaryColor;
}

.login-container {
  position: relative;
